<template>
	<div class="circle">
		<br />
		<van-field
			v-model.number="state.circleId"
			type="digit"
			label="话题id："
			placeholder="请输入话题id"
		/>
		<br />
		<div>
			<van-radio-group v-model="state.typeId">
				<van-radio :name="1">问答</van-radio>
				<van-radio :name="2">日常</van-radio>
			</van-radio-group>
		</div>
		<br />
		<van-button type="primary" @click="onCircle1">前往发布页（ylcloser）</van-button>
		&nbsp;<van-button type="primary" @click="onCircle2">前往发布页（交互协议）</van-button>
		<!-- <br /> -->
		<!-- &nbsp; -->
		<van-button type="primary" @click="onCircleDetails">前往圈子详情页</van-button>
		<br />
		<br />
		<van-field
			v-model.number="state.postId"
			type="digit"
			label="帖子id："
			placeholder="请输入帖子id"
		/>
		<van-button type="primary" @click="onDetails(1)">前往详情页（问答）</van-button>
		&nbsp;<van-button type="primary" @click="onDetails(2)">前往详情页（日常）</van-button>
		<br />
		<br />

		<van-field
			v-model.number="state.topicId"
			type="digit"
			label="话题id："
			placeholder="请输入话题id"
		/>

		<van-button type="primary" @click="onTopicDetails">前往话题详情页</van-button>
		<br /><br />

		<van-field v-model="state.userId" label="userid：" placeholder="请输入userid" />

		<van-button type="primary" @click="onUserInfo">前往用户信息页</van-button>
		<br />
	</div>
</template>
<script lang="ts" setup>
import { reactive } from "vue"
import { xdJsbridge } from "wnl-xdsdk"
const state = reactive({
	circleId: undefined,
	topicId: undefined,
	userId: undefined,
	postId: undefined,
	typeId: 2
})


// 前往发布页（ylcloser）
const onCircle1 = () => {
	if (!state.circleId) {
		xdJsbridge.toast("话题id不能为空")
		return
	}

	xdJsbridge.jumpPage(`ylcloser://jumpAskQuestion?type=${state.typeId}&topicId=${state.circleId}`)
}

// 前往发布页（交互协议）
const onCircle2 = () => {
	if (!state.circleId) {
		xdJsbridge.toast("话题id不能为空")
		return
	}

	xdJsbridge.invokeAction({
		command: "jumpDynamic",
		args: {
			// image: [], // Array 图片 可选 {image: string, width: number, height: number }[]
			// content: "", // String 内容 可选
			topicId: state.circleId, // 话题id, 可选
			type: state.typeId // 1 问答 2 日常
		}
	})
}

// 圈子详情页
const onCircleDetails = () => {
	if (!state.circleId) {
		xdJsbridge.toast("话题id不能为空")
		return
	}

	xdJsbridge.jumpPage(`ylcloser://socialCircle?socialCircleId=${state.circleId}`)
}
// 前往帖子详情页
const onDetails = (type: number) => {
	if (!state.postId) {
		xdJsbridge.toast("帖子id不能为空")
		return
	}

	if (type === 1) {
		// 问答
		xdJsbridge.jumpPage(`ylcloser://mondoDetail?questionId=${state.postId}`)
	} else {
		// 日常
		xdJsbridge.jumpPage(`ylcloser://momentDetail?momentId=${state.postId}`)
	}
}

// 前往话题详情页
const onTopicDetails = () => {
	if (!state.topicId) {
		xdJsbridge.toast("话题id不能为空")
		return
	}

	xdJsbridge.jumpPage(`ylcloser://topicDetail?topicId=${state.topicId}`)
}

// 前往用户信息页
const onUserInfo = () => {
	if (!state.topicId) {
		xdJsbridge.toast("userid不能为空")
		return
	}
	xdJsbridge.jumpPage(`ylcloser://momentPerson?userId=${state.userId}`)
}
</script>

<style lang="scss" scoped>
.circle {
	width: 100%;
	min-height: 100vh;
	background-color: red;
	font-size: 24px;
}
</style>
